export type ScanResults = GoogleScanResults | LinkedInScanResults;

export type LinkedInScanResults = {};

export type GoogleScanType =
  | "thirdPartyApps"
  | "profileInfo"
  | "securityDetails"
  | "deviceSessions";

export type GoogleScanResults = {
  securityDetails?: GoogleSecurityDetails;
  thirdPartyApps?: GoogleThirdPartyApp[];
  deviceSessions?: GoogleDeviceSessions;
  profileInfo?: GoogleProfileInfo;
};

export type GoogleSecurityDetails = {
  recoveryPhone?: string;
  recoveryEmail?: string;
  passwordLastChange?: Date;
  twoFactorStatus?: "on" | "off";
};

export type GoogleThirdPartyApp = {
  id: string;
  name: string;
  homePage: string;
  permissions: GoogleThirdPartyAppPermission[];
  accessGivenOn?: string;
  accessGivenTo?: string;
};

export type GoogleThirdPartyAppPermission = {
  title: string;
  description: string;
  weight: number;
};

export type GoogleDeviceSessions = Record<string, GoogleDeviceSession>;

export type GoogleDeviceSession = {
  deviceName?: string;
  deviceType?: string;
  sessionId: string;
  isThisDevice: boolean;
  firstSeen?: string;
  lastSeen?: string;
  location?: string;
  formFactorNum?: number;
  osNum?: number;
};

export type GoogleProfileInfoVisibility = "public" | "private";
type ProfileInfoName = {
  value: string;
  visibility: GoogleProfileInfoVisibility;
};
type ProfileInfoProfilePicture = {
  value?: string;
  visibility: GoogleProfileInfoVisibility;
};
type ProfileInfoGender = {
  value?: string;
  visibility: GoogleProfileInfoVisibility;
};
type ProfileInfoBirthday = {
  value?: string;
  visibility: GoogleProfileInfoVisibility;
};
type ProfileEmails = {
  googleAccountEmail?: {
    value?: string;
    visibility: GoogleProfileInfoVisibility;
  };
  otherEmails?: {
    value?: string;
    visibility: GoogleProfileInfoVisibility;
  }[];
};
type ProfilePlaces = {
  current?: string;
  previous?: string[];
  visibility: GoogleProfileInfoVisibility;
};
type ProfileOccupation = {
  value?: string;
  visibility: GoogleProfileInfoVisibility;
};
type ProfileEducation = {
  value?: string;
  visibility: GoogleProfileInfoVisibility;
};

export type GoogleProfileInfo = {
  name?: ProfileInfoName;
  profilePicture?: ProfileInfoProfilePicture;
  gender?: ProfileInfoGender;
  birthday?: ProfileInfoBirthday;
  emails?: ProfileEmails;
  places?: ProfilePlaces;
  occupation?: ProfileOccupation;
  education?: ProfileEducation;
};

export type OAPRecommendation = {
  scanType: ScanType;
  type: OAPRecommendationType;
  importance: Importance;
  resolved: boolean;
  categoryLabel: string;
  skipped?: boolean;
  unresolvedInfo?: OAPRecommendationInfo;
  resolvedInfo?: OAPRecommendationInfo;
  whatToDoTitle?: string;
};

export type OAPRecommendationInfo = {
  title: string;
  explanation: string;
  actionMessage?: string;
  whatToDo?: string[];
}

export type Importance = "low" | "medium" | "high";
export type ScanType = "google" | "linkedin" | "instagram" | "x";

export type GoogleRecommendationTypes =
  | "google-apps-basic-access"
  | "google-apps-extended-access"
  | "google-connected-devices"
  | "google-2fa-status"
  | "google-recovery-email"
  | "google-recovery-phone"
  | "google-password-change"
  | "google-public-profile-info";

export type OAPRecommendationType = GoogleRecommendationTypes;

// Check if scan results are for Google using type guard
export function isGoogleScanResults(
  results: ScanResults
): results is GoogleScanResults {
  return (
    "securityDetails" in results ||
    "thirdPartyApps" in results ||
    "deviceSessions" in results ||
    "profileInfo" in results
  );
}

export const RESULTS_VIEW_PAGE_STORAGE_KEY = "oapResultsViewPage";

export type OAPMarkAsDoneRequest = {  
  recommendation: OAPRecommendation;
};

export type OAPMarkAsDoneResponse = {
  success: boolean;
};

export type OAPSkipRequest = {
  recommendation: OAPRecommendation;
};

export type OAPSkipResponse = {
  success: boolean;
};

export type OAPBringBackRecommendationRequest = {
  recommendation: OAPRecommendation;
};

export type OAPBringBackRecommendationResponse = {
  success: boolean;
};