import {
  GoogleDeviceSession,
  GoogleDeviceSessions,
  GoogleScanResults,
  GoogleThirdPartyApp,
  OAPRecommendation,
  ScanResults,
  ScanType,
} from "./types";

const SCAN_STORAGE_KEY = "oapScanResults";

export interface CompletedScan {
  scanType: ScanType;
  scanResults: ScanResults;
  recommendations: OAPRecommendation[];
  startDate: string;
  endDate: string;
}

export class ScanStore {
  public constructor() {}

  public async saveScan(scanType: ScanType, scan: CompletedScan) {
    console.debug("ScanStore: Saving scan results", scanType, scan);
    try {
      const result = await chrome.storage.local.get([SCAN_STORAGE_KEY]);
      const scans = result[SCAN_STORAGE_KEY] || {};
      console.debug("ScanStore: Existing scans", scans);
      scans[scanType] = scan;
      console.debug("ScanStore: Updated scans", scans);

      await chrome.storage.local.set({
        [SCAN_STORAGE_KEY]: scans,
      });
      console.log("ScanStore: Scan saved");
    } catch (err) {
      console.error("ScanStore: Error saving scan results", err);
      throw err;
    }
  }

  public async getScans(): Promise<CompletedScan[]> {
    const result = await chrome.storage.local.get([SCAN_STORAGE_KEY]);
    const scans = result[SCAN_STORAGE_KEY] || {};
    return Object.values(scans) as CompletedScan[];
  }

  public async getScan(scanType: ScanType): Promise<CompletedScan | undefined> {
    const result = await chrome.storage.local.get([SCAN_STORAGE_KEY]);
    const scans = result[SCAN_STORAGE_KEY] || {};
    return scans[scanType] as CompletedScan | undefined;
  }

  public async markRecommendationAsDone(
    recommendation: OAPRecommendation
  ): Promise<OAPRecommendation | undefined> {
    console.debug("ScanStore: Marking recommendation as done", recommendation);
    try {
      const result = await chrome.storage.local.get([SCAN_STORAGE_KEY]);
      const scans = result[SCAN_STORAGE_KEY] || {};
      let scan = scans[recommendation.scanType];
      if (scan) {
        scan = scan as CompletedScan;

        const targetRecommendation = scan.recommendations.find(
          (r) => r.type === recommendation.type
        );
        if (targetRecommendation) {
          (targetRecommendation as OAPRecommendation).resolved = true;
          scans[recommendation.scanType] = scan;
          await chrome.storage.local.set({
            [SCAN_STORAGE_KEY]: scans,
          });
          console.log("ScanStore: Recommendation marked as done");
          return targetRecommendation;
        }
      }
      return undefined;
    } catch (err) {
      console.error("ScanStore: Error marking recommendation as done", err);
      throw err;
    }
  }

  public async skipRecommendation(
    recommendation: OAPRecommendation
  ): Promise<OAPRecommendation | undefined> {
    console.debug("ScanStore: Skipping recommendation", recommendation);
    try {
      const result = await chrome.storage.local.get([SCAN_STORAGE_KEY]);
      const scans = result[SCAN_STORAGE_KEY] || {};
      let scan = scans[recommendation.scanType];
      if (scan) {
        scan = scan as CompletedScan;
        const targetRecommendation = scan.recommendations.find(
          (r) => r.type === recommendation.type
        );
        if (targetRecommendation) {
          (targetRecommendation as OAPRecommendation).skipped = true;
          scans[recommendation.scanType] = scan;
          await chrome.storage.local.set({
            [SCAN_STORAGE_KEY]: scans,
          });

          console.log("ScanStore: Recommendation marked as skipped");
          return targetRecommendation;
        }
      }
      return undefined;
    } catch (err) {
      console.error("ScanStore: Error marking recommendation as skipped", err);
      throw err;
    }
  }

  public async bringBackRecommendation(
    recommendation: OAPRecommendation
  ): Promise<OAPRecommendation | undefined> {
    console.debug("ScanStore: Bringing back recommendation", recommendation);
    try {
      const result = await chrome.storage.local.get([SCAN_STORAGE_KEY]);
      const scans = result[SCAN_STORAGE_KEY] || {};
      let scan = scans[recommendation.scanType];
      if (scan) {
        scan = scan as CompletedScan;
        const targetRecommendation = scan.recommendations.find(
          (r) => r.type === recommendation.type
        );
        if (targetRecommendation) {
          (targetRecommendation as OAPRecommendation).resolved = false;
          (targetRecommendation as OAPRecommendation).skipped = false;
          scans[recommendation.scanType] = scan;
          await chrome.storage.local.set({
            [SCAN_STORAGE_KEY]: scans,
          });

          console.log("ScanStore: Recommendation unskipped");
          return targetRecommendation;
        }
      }
      return undefined;
    } catch (err) {
      console.error("ScanStore: Error unskipping recommendation", err);
      throw err;
    }
  }

  public async googleUpdateWithDeletedConnection(
    recommendation: OAPRecommendation,
    app: GoogleThirdPartyApp
  ): Promise<OAPRecommendation | undefined> {
    console.debug("ScanStore: Updating with deleted connection", app);
    try {
      const result = await chrome.storage.local.get([SCAN_STORAGE_KEY]);
      const scans = result[SCAN_STORAGE_KEY] || {};
      let scan = scans["google"];
      if (scan) {
        console.debug("ScanStore: Scan", scan);
        scan = scan as CompletedScan;
        const scanResults = scan.scanResults as GoogleScanResults;
        let basicAccessApps =
          scanResults.thirdPartyApps?.filter(
            (app) =>
              app.permissions.length === 1 && app.permissions[0].weight === 0
          ) || [];
        let extendedAccessApps =
          scanResults.thirdPartyApps?.filter((app) =>
            app.permissions.some((p) => p.weight > 0)
          ) || [];

        if (recommendation.type === "google-apps-basic-access") {
          basicAccessApps = basicAccessApps?.filter(
            (a) => !!a.name === false || a.name !== app.name
          );
        } else if (recommendation.type === "google-apps-extended-access") {
          extendedAccessApps = extendedAccessApps?.filter(
            (a) => !!a.name === false || a.name !== app.name
          );
        }

        const basicAccessAppsCount = basicAccessApps?.length || 0;
        const extendedAccessAppsCount = extendedAccessApps?.length || 0;
        scanResults.thirdPartyApps = [
          ...basicAccessApps,
          ...extendedAccessApps,
        ];
        scan.scanResults = scanResults;

        // Update recommendations
        const recommendations = scan.recommendations;
        const targetRecommendation = recommendations.find(
          (r) => r.type === recommendation.type
        );
        if (targetRecommendation) {
          if (recommendation.type === "google-apps-basic-access") {
            if (basicAccessAppsCount === 0) {
              targetRecommendation.resolved = true;
            } else {
              if (
                targetRecommendation.unresolvedInfo &&
                Array.isArray(targetRecommendation.unresolvedInfo.whatToDo) &&
                targetRecommendation.unresolvedInfo.whatToDo.length > 0
              ) {
                targetRecommendation.unresolvedInfo.whatToDo[0] = `<route '${recommendation.type}'>View ${basicAccessAppsCount} apps</route>`;
              }
            }
          } else if (recommendation.type === "google-apps-extended-access") {
            if (extendedAccessAppsCount === 0) {
              targetRecommendation.resolved = true;
            } else {
              if (
                targetRecommendation.unresolvedInfo &&
                Array.isArray(targetRecommendation.unresolvedInfo.whatToDo) &&
                targetRecommendation.unresolvedInfo.whatToDo.length > 0
              ) {
                targetRecommendation.unresolvedInfo.whatToDo[0] = `<route '${recommendation.type}'>View ${extendedAccessAppsCount} apps</route>`;
              }
            }
          }
        }

        const updatedRecommendations = [
          ...recommendations.filter((r) => r.type !== recommendation.type),
          targetRecommendation,
        ];
        scan.recommendations = updatedRecommendations;

        scans["google"] = scan;
        await chrome.storage.local.set({
          [SCAN_STORAGE_KEY]: scans,
        });

        console.log("ScanStore: Connection deleted");
        return targetRecommendation;
      }

      return undefined;
    } catch (err) {
      console.error("ScanStore: Error updating with deleted connection", err);
      throw err;
    }
  }

  public async googleUpdateWithDeletedDeviceSession(
    recommendation: OAPRecommendation,
    device: GoogleDeviceSession
  ): Promise<OAPRecommendation | undefined> {
    console.debug("ScanStore: Updating with deleted device session", device);
    try {
      const result = await chrome.storage.local.get([SCAN_STORAGE_KEY]);
      const scans = result[SCAN_STORAGE_KEY] || {};
      let scan = scans["google"];
      if (scan) {
        console.debug("ScanStore: Scan", scan);
        scan = scan as CompletedScan;
        const scanResults = scan.scanResults as GoogleScanResults;
        console.debug("ScanStore: Scan results", scanResults);
        const deviceSessionsArray = scanResults.deviceSessions
          ? Object.values(scanResults.deviceSessions)
          : [];
        const updatedDeviceSessions = deviceSessionsArray.filter(
          (d) => d.sessionId !== device.sessionId
        );
        const updatedSessionCount = updatedDeviceSessions.length;
        scanResults.deviceSessions = updatedDeviceSessions.reduce(
          (acc, device) => {
            acc[device.sessionId] = device;
            return acc;
          },
          {} as GoogleDeviceSessions
        );
        scan.scanResults = scanResults;

        // Update recommendations
        const recommendations = scan.recommendations;
        const connectedDevicesRecommendation = recommendations.find(
          (r) => r.type === recommendation.type
        );
        if (connectedDevicesRecommendation) {
          if (updatedSessionCount === 0) {
            connectedDevicesRecommendation.resolved = true;
          } else {
            if (
              connectedDevicesRecommendation.unresolvedInfo &&
              Array.isArray(
                connectedDevicesRecommendation.unresolvedInfo.whatToDo
              ) &&
              connectedDevicesRecommendation.unresolvedInfo.whatToDo.length > 0
            ) {
              connectedDevicesRecommendation.unresolvedInfo.whatToDo[0] = `<route '${recommendation.type}'>View ${updatedSessionCount} devices</route>`;
            }
          }
        }
        const updatedRecommendations = [
          ...recommendations.filter((r) => r.type !== recommendation.type),
          connectedDevicesRecommendation,
        ];
        scan.recommendations = updatedRecommendations;

        scans["google"] = scan;

        await chrome.storage.local.set({
          [SCAN_STORAGE_KEY]: scans,
        });
        console.log(
          "OAP.ScanStore: Device session deleted",
          updatedSessionCount
        );
        return connectedDevicesRecommendation;
      }
      return undefined;
    } catch (err) {
      console.error(
        "ScanStore: Error updating with deleted device session",
        err
      );
      throw err;
    }
  }
}
